/* http://meyerweb.com/eric/tools/css/reset/
   v5.0.1 | 20191019
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, menu, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

menu, ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

* {
  box-sizing: border-box;
}

.srOnly {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
}

.wrapper {
  padding: 7.2rem 3.2rem 7.6rem;
  font-size: 1.6rem;
  background: #e5eff5;
}
@media (min-width: 48rem) {
  .wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    padding: 0;
  }
}

.offer {
  overflow: hidden;
  font-family: Karla, sans-serif;
  border-radius: 0.4rem;
  box-shadow: 0 2rem 2.4rem rgba(0, 0, 0, 0.1);
}
@media (min-width: 48rem) {
  .offer {
    width: 63.5rem;
    border-radius: 0.8rem;
  }
}
.offer_product {
  padding: 2.4rem 2.4rem 3.2rem;
  background: white;
}
@media (min-width: 48rem) {
  .offer_product {
    padding: 2.4rem 4rem 4rem;
  }
}
.offer_subscription {
  padding: 2.4rem;
  background: #2ab2af;
}
@media (min-width: 48rem) {
  .offer_subscription {
    padding: 4rem;
  }
}
.offer_benefits {
  padding: 2.4rem;
  background: #4bbebc;
}
@media (min-width: 48rem) {
  .offer_benefits {
    padding: 4rem;
  }
}
@media (min-width: 48rem) {
  .offer_container {
    display: flex;
  }
  .offer_container > section {
    width: 50%;
  }
}
.offer_heading {
  margin: 0;
  padding: 2.8rem 2.4rem 0;
  background: white;
}
@media (min-width: 48rem) {
  .offer_heading {
    padding: 4rem 4rem 0;
  }
}
.offer_productText {
  margin: 1.6rem 0 0;
}
@media (min-width: 48rem) {
  .offer_productText {
    margin-top: 0.8rem;
  }
}
.offer_subscriptionText {
  margin: 0.2rem 0 0;
}
.offer_benefitsList {
  margin: 1.6rem 0 0;
  padding: 0;
  list-style: none;
}
.offer_benefitsList p {
  margin: 0;
}
.offer_price {
  display: flex;
  align-items: center;
  margin-top: 1.6rem;
  color: #e5eff5;
  letter-spacing: -0.012em;
}
.offer_price > span {
  margin-right: 1.1rem;
  color: white;
  font-weight: 700;
  font-size: 3.2rem;
  line-height: 120%;
}

.heading {
  color: #2ab2af;
  font-weight: 700;
  font-size: 2rem;
  line-height: 115%;
  letter-spacing: -0.012em;
}
@media (min-width: 48rem) {
  .heading {
    font-size: 2.4rem;
    line-height: 120%;
  }
}

.subheading {
  margin: 0;
  color: white;
  font-weight: 700;
  font-size: 1.8rem;
  line-height: 125%;
  letter-spacing: -0.012em;
}
.subheading-accent {
  color: #c0df34;
  font-size: 1.6rem;
  letter-spacing: -0.044em;
}
@media (min-width: 48rem) {
  .subheading-accent {
    font-size: 1.8rem;
    line-height: 135%;
    letter-spacing: -0.012em;
  }
}

.text {
  color: #98a6bd;
  font-size: 1.4rem;
  line-height: 190%;
  letter-spacing: -0.012em;
}
@media (min-width: 48rem) {
  .text {
    font-size: 1.6rem;
    line-height: 165%;
  }
}
.text-light {
  color: white;
}
.text-lg {
  font-size: 1.6rem;
  line-height: 190%;
}
.text-sm {
  color: #e5eff5;
  font-size: 1.4rem;
  line-height: 145%;
}

.button {
  width: 100%;
  margin-top: 2.4rem;
  padding: 1.3rem;
  color: white;
  font: 700 1.6rem Karla, sans-serif;
  background: #c0df34;
  border: 0.2rem solid #c0df34;
  border-radius: 0.4rem;
  box-shadow: 0 1rem 0.8rem rgba(0, 0, 0, 0.1);
  transition: all 0.2s;
}
@media (min-width: 48rem) {
  .button {
    border-radius: 0.6rem;
  }
}
.button:active {
  background: #afcf20;
  box-shadow: 0 0.5rem 0.4rem rgba(0, 0, 0, 0.1);
}
.button:focus {
  border: 0.2rem solid white;
  outline: none;
}
.button:hover {
  background: #cae453;
  box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.2);
}